.PlayButton {
    background-color: #353348;
    font-size: 10pt;
    padding: 3px 10px;
    margin: 10px 5px;
    color: white;
    cursor: pointer;
    border-radius: 7px;
    border: 1px solid #504d64;
}

.PlayButton:hover {
    background-color: #2d2b3d;
}

.PlayButton--img {
    width: 32px;
}